body
{
  background: linear-gradient(45deg, rgba(57,204,204,1) 0%, rgba(255,255,255,1) 100%);
  background-size:100vw 100vh;
}
.App {
  margin-left: auto;
  margin-right: auto;
  width: 80vw;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;

}
ul
{
  list-style-type: none;
}
li
{
  margin-left: 1vw;
  display: flex;
  align-items: center;
}
.text
{
  width: 20vw;
}
.todo
{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  width: 50vw;
  height: 50px;
  margin-bottom: 30px;
}
.todo-list
{
  padding: 0;
}
select
{
  height: 40px;
  width: 23vw;
  border:0px solid;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  cursor: pointer;
}
.select
{ 
  color:white;
  position: relative;
}
.select::after {
  content: "\25BC";
  position: absolute;
  text-align: center;
  box-sizing: border-box;
  padding: 10px;
  top: 0;
  right: 0;
  height: 100%;
  width: 40px;
  background: #39CCCC;
  cursor: pointer;
  pointer-events: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: 250ms;
}
.completed
{
  text-decoration: line-through;
  transition: 500ms;
  filter: opacity(50%);
}
.priority
{
  font-weight: 700;
}
.btn
{
  color:white;
  font-size: 18px;
  height: 50px;
  width: 50px;
}
.btn:hover
{
  filter:brightness(90%);
  border:0 solid;
  cursor: pointer;
}
.completed-btn
{
  background-color: red;
  border:0 solid;
  background: linear-gradient(225deg, rgba(0,255,0,1) 0%, rgba(100,255,100,1) 100%);
}
.edit-btn
{
  background: linear-gradient(225deg, rgba(0,0,255,1) 0%, rgba(61,61,255,1) 100%);
  border:0 solid;
}
.priority-btn
{
  background: linear-gradient(225deg, rgba(100,61,61,1) 0%, rgba(255,0,0,1) 100%);
  border:0 solid;
}
.trash-btn
{
  transition: 250ms;
  background-size: 0px;
  background: linear-gradient(225deg, rgba(255,0,0,1) 0%, rgba(255,51,51,1) 100%);
  border:0 solid;
}
.mini-tag
{
  width: 10px;
  height: 10px;
  background-color: red;
}
.todo-button
{
  color:#39CCCC;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  background-color: white;
  text-align: center;
  height: 40px;
  border:0px solid;
  margin-right: 4vw;
  transition: 250ms;
  width: 3vw;
}
form{
  margin-left: auto;
  margin-right: auto;
  width: 50vw;
  display: flex;
  justify-content: center;
}
.todo-button:hover
{
  color:white;
  background-color: #39CCCC;
  cursor: pointer;
}
.select:hover::after
{
  background-color:white;
  color: #39CCCC;
  cursor: pointer;
}
form
{
  display: flex;
}
input
{
  border:0px solid;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}